
.ui.vertical.segment.site-footer {
  padding: 1em;
  margin-top: 2em;
}

.top-nav .logo {
  height: 32px;
  width: 32px;
}

#root {
  min-height: 100vh;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.root {
  flex: 1 1 100%;
}

.missing-page {
  margin-top: 25vh;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  -webkit-transform-origin: top;
          transform-origin: top;
}

.label.type {
  min-width: 64px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

/*
 * Low resolution displays, like laptops, need more text shadow than high
 * resolution displays, like phones.
 */
@media (-webkit-min-device-pixel-ratio: 2.0833333333333335), (min-resolution: 200dpi) {
  .label.type .inner {
    text-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15);
  }
}
@media (-webkit-max-device-pixel-ratio: 2.0833333333333335), (max-resolution: 200dpi) {
  .label.type .inner {
    text-shadow: 0px 0px 2px rgba(0, 0, 0, 0.3);
  }
}

.label.type .inner {
  -webkit-transform-origin: left;
          transform-origin: left;
  display: block;
}

.label.type.normal {
  background-color: #aa9;
}

.label.type.fire {
  background-color: #f42;
}

.label.type.water {
  background-color: #39f;
}

.label.type.electric {
  background-color: #fc3;
  padding-left: 8px;
  padding-right: 8px;
}

.label.type.electric .inner {
  -webkit-transform: scale(0.9, 1);
          transform: scale(0.9, 1);
  width: 45px;
}

.label.type.grass {
  background-color: #7c5;
}

.label.type.ice {
  background-color: #6cf;
}

.label.type.fighting {
  background-color: #b54;
  padding-left: 8px;
  padding-right: 8px;
}

.label.type.fighting .inner {
  -webkit-transform: scale(0.85, 1);
          transform: scale(0.85, 1);
  width: 48px;
}

.label.type.poison {
  background-color: #a59;
}

.label.type.ground {
  background-color: #db5;
  padding-left: 8px;
  padding-right: 8px;
}

.label.type.ground .inner {
  -webkit-transform: scale(0.9, 1);
          transform: scale(0.9, 1);
  width: 45px;
}

.label.type.flying {
  background-color: #89f;
}

.label.type.psychic {
  background-color: #f59;
}

.label.type.bug {
  background-color: #ab2;
}

.label.type.rock {
  background-color: #ba6;
}

.label.type.ghost {
  background-color: #66b;
}

.label.type.dragon {
  background-color: #76e;
}

.label.type.dark {
  background-color: #754;
}

.label.type.steel {
  background-color: #aab;
}

.label.type.fairy {
  background-color: #e9e;
}

.type-multiplier:not(:last-of-type) {
  margin-bottom: 0.4rem;
}

.ui.label.multiplier-badge {
  box-sizing: border-box;
  font-size: 1rem;
  padding: 0.4em !important;
  min-height: 1.9rem;
  min-width: 1.9rem;
  width: 1.9rem;
  height: 1.9rem;
  line-height: 1rem;
  position: relative;
  font-weight: 400;
}

.ui.label.multiplier-badge.fraction span {
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  display: block;
}

.pokemon-sprite {
  height: 96px;
  width: 96px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

